<template>
    <div>
        <el-form
                ref="loginForm"
                :rules="rules"

                v-loading="loading"
                element-loading-text="..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"

                :model="loginForm"
                class="loginContainer">
            <h3 class="loginTitle"></h3>
            <!-- prop  username  -->
            <el-form-item prop="username">
                <el-input type="text" v-model="loginForm.username" auto-complete="off"
                          size="normal"
                          placeholder="" @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input type="password" v-model="loginForm.password" auto-complete="off"
                          size="normal"
                          placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="code">
                <el-input type="text" v-model="loginForm.code" auto-complete="off"
                          size="normal"
                          placeholder="" @keydown.enter.native="submitLogin" style="width: 250"></el-input>
                <img :src="vcUrl" @click="updateVerifyCode" alt="">
            </el-form-item>
            <el-checkbox size="normal" class="loginRemember" v-model="checked"></el-checkbox>
            <el-button size="normal" type="primary" style="width: 100%" @click="submitLogin"></el-button>
        </el-form>
    </div>
</template>

<script>
    //  main.js 
    // import {postKeyValueRequest} from "@/utils/api";

    export default {
        name: "Login",
        data() {
            return {
                //  url 
                vcUrl: '/verifyCode?time=' + new Date(),
                // 
                code: '',
                // 
                loading: false,
                //   
                rules: {
                    username: [{required: true, message: '', trigger: 'blur'}],
                    password: [{required: true, message: '', trigger: 'blur'}],
                    code: [{required: true, message: '', trigger: 'blur'}]
                },
                //  
                loginForm: {
                    username: '',
                    password: ''
                },
                checked: false
            }
        },
        methods: {
            updateVerifyCode() {
                // 
                this.vcUrl = '/verifyCode?time=' + new Date();
            },
            submitLogin() {
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        this.loading = true; // 
                        //  import  this.xxxx 
                        this.postKeyValueRequest('/doLogin', this.loginForm).then(resp => {
                            this.loading = false  // 
                            //  
                            if (resp) {
                                this.$store.commit('INIT_CURRENTHR', resp.obj)
                                //  storage  
                                window.sessionStorage.setItem("user", JSON.stringify(resp.obj))
                                //  path  path 
                                let path = this.$route.query.redirect;  // redirect  main.js 
                                //   $router  main.js 
                                // this.$router.push()  //  login 
                                this.$router.replace((path === '/' || path === undefined) ? '/home' : path)  //  login 
                            } else {
                                // 
                                this.vcUrl = '/verifyCode?time=' + new Date();
                            }
                        })
                    } else {
                        this.$message.error('')
                        return false;
                    }
                })
            }
        }
    }
</script>

<style>
    .loginContainer {
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        width: 350px;
        padding: 15px 35px 15px 35px;
        background: #ffffff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #cac6c6;
    }

    .loginTitle {
        margin: 15px auto 20px auto;
        text-align: center;
        color: #505458;
    }

    .loginRemember {
        text-align: left;
        margin: 0 0 15px 0;
    }

    .el-form-item__content {
        display: flex;
        align-items: center;
    }
</style>