<template>
    <div>
        <el-container>
            <el-header class="homeHeader">
                <div class="title"></div>
                <div>
                    <el-button icon="el-icon-bell" type="text" style="margin-right: 10px;color: #000000;" size="normal" @click="goChat"></el-button>
                    <el-dropdown class="userInfo" @command="commandHandler">
                  <span class="el-dropdown-link">
                    {{user.name}}
                      <i>
                        <img :src='user.userFace' alt="">
                      </i>
                  </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userinfo"></el-dropdown-item>
                            <el-dropdown-item command="setting"></el-dropdown-item>
                            <el-dropdown-item command="logout" divided></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px">
                    <!--  router   index   menuClick  -->
                    <!-- <el-menu @select="menuClick" router>-->
                    <el-menu router unique-opened>
                        <!--  router/index.js  routes  hidden  false  router -->
                        <!--  computed  routes -->
                        <el-submenu :index="index + ' '" v-for="(item,index) in routers" v-if="!item.hidden">
                            <template slot="title">
                                <i style="color: #5395e5; margin-right: 5px" :class="item.iconCls"></i>
                                <span>{{item.name}}</span>
                            </template>
                            <el-menu-item v-for="(child,indexj) in item.children" :index="child.path" :key="indexj">
                                {{child.name}}
                            </el-menu-item>
                        </el-submenu>
                    </el-menu>
                </el-aside>
                <el-main>
                    <!--  -->
                    <el-breadcrumb separator-class="el-icon-arrow-right"
                                   v-if="this.$router.currentRoute.path !== '/home'">
                        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
                        <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                    </el-breadcrumb>
                    <div class="homeWelcome" v-if="this.$router.currentRoute.path === '/home'">
                        
                    </div>
                    <router-view class="homeRouterView"/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Home",
        data() {
            return {
                //  storage    Json
                // user: JSON.parse(window.sessionStorage.getItem('user')),
            }
        },
        computed: {
            routers() {
                return this.$store.state.routes;
            },
            user() {
                return this.$store.state.currentHr
            }
        },
        methods: {
            goChat() {
                this.$router.push('/chat');
            },
            commandHandler(cmd) {
                if (cmd === 'logout') {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        //   /logout 
                        this.getRequest('/logout');
                        // 
                        window.sessionStorage.removeItem('user');
                        // 
                        this.$router.replace('/');
                        //  storage 
                        this.$store.commit('initRoutes', [])
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                }else if (cmd === "userinfo") {
                    this.$router.push('/hrinfo')
                }
            },
            // menuClick(index) {
            //     // index  <el-menu-item index="/test1"></el-menu-item>  index
            //     this.$router.push(index)
            // }
        }
    }
</script>

<style>
    .homeRouterView {
        margin-top: 10px;
    }

    .homeWelcome {
        text-align: center;
        font-size: 35px;
        font-family: ;
        color: #409eff;
        padding-top: 50px;
    }

    .homeHeader {
        background-color: #409eff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 15px;
        box-sizing: border-box;
    }

    .homeHeader .title {
        font-size: 30px;
        font-family: ;
        color: #f1f6fa;
    }

    .homeHeader .userInfo {
        cursor: pointer;
    }

    .el-dropdown-link img {
        width: 36px;
        height: 36px;
        border-radius: 24px;
        margin-left: 8px;
    }

    .el-dropdown-link {
        display: flex;
        align-items: center;
    }

</style>
